@import url(https://fonts.googleapis.com/css?family=opensans:500);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  /*background-image: url(http://kreativo.se/backlogin.jpg);*/
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #8be8cf));
  font-family: "Helvetica Neue", Helvetica, Arial;
  padding-top: 20px; }

.container {
  width: 406px;
  max-width: 406px;
  margin: 0 auto; }

#signup {
  padding: 0px 25px 25px;
  background: #fff;
  box-shadow: 0px 0px 0px 5px rgba(255, 255, 255, 0.4), 0px 4px 20px rgba(0, 0, 0, 0.33);
  border-radius: 5px;
  display: table;
  position: static; }

#signup .header {
  margin-bottom: 20px; }

#signup .header h3 {
  color: #333333;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 5px; }

#signup .header p {
  color: #8f8f8f;
  font-size: 14px;
  font-weight: 300; }

#signup .sep {
  height: 1px;
  background: #e8e8e8;
  width: 406px;
  margin: 0px -25px; }

#signup .inputs {
  margin-top: 25px; }

#signup .inputs label {
  color: #8f8f8f;
  font-size: 12px;
  font-weight: 300;
  letter-spacing: 1px;
  margin-bottom: 7px;
  display: block; }

input::-webkit-input-placeholder {
  color: #b5b5b5; }

input:-moz-placeholder {
  color: #b5b5b5; }

#signup .inputs input[type=email], input[type=password] {
  background: #f5f5f5;
  font-size: 0.8rem;
  border-radius: 3px;
  border: none;
  padding: 13px 10px;
  width: 330px;
  margin-bottom: 20px;
  box-shadow: inset 0px 2px 3px rgba(0, 0, 0, 0.1);
  clear: both; }

#signup .inputs input[type=email]:focus, input[type=password]:focus {
  background: #fff;
  box-shadow: 0px 0px 0px 3px #fff38e, inset 0px 2px 3px rgba(0, 0, 0, 0.2), 0px 5px 5px rgba(0, 0, 0, 0.15);
  outline: none; }

#signup .inputs .checkboxy {
  display: block;
  position: static;
  height: 25px;
  margin-top: 10px;
  clear: both; }

#signup .inputs input[type=checkbox] {
  float: left;
  margin-right: 10px;
  margin-top: 3px; }

#signup .inputs label.terms {
  float: left;
  font-size: 14px;
  font-style: italic; }

#signup .inputs #submit {
  width: 100%;
  margin-top: 20px;
  padding: 15px 0;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  text-align: center;
  text-decoration: none;
  background: -moz-linear-gradient(top, #b9c5dd 0%, #a4b0cb);
  background: -webkit-gradient(linear, left top, left bottom, from(#b9c5dd), to(#a4b0cb));
  border-radius: 5px;
  border: 1px solid #737b8d;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.1), inset 0px 1px 0px rgba(255, 255, 255, 0.5);
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3), 0px 0px 0px rgba(255, 255, 255, 0);
  display: table;
  position: static;
  clear: both; }

#signup .inputs #submit:hover {
  background: -moz-linear-gradient(top, #a4b0cb 0%, #b9c5dd);
  background: -webkit-gradient(linear, left top, left bottom, from(#a4b0cb), to(#b9c5dd)); }

.img-container {
  margin-top: 30vh;
  width: 100vw;
  padding-bottom: 56.25%;
  position: relative;
  background: #fff; }

img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: auto; }

